'use client';

import { useState } from 'react';
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";

export default function ScraperPage() {
  const [url, setUrl] = useState('');
  const [result, setResult] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [copySuccess, setCopySuccess] = useState('');

  const handleScrape = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setResult(null);
    setCopySuccess('');

    if (!url) {
      setError('Please enter a valid URL');
      return;
    }

    setLoading(true);
    try {
      const response = await fetch('/api/scrape', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ url })
      });
      
      const data = await response.json();
      
      if (!response.ok) {
        throw new Error(data.error || 'Failed to scrape the article');
      }
      
      setResult(data);
    } catch (err: any) {
      setError(err.message || 'An unexpected error occurred');
    } finally {
      setLoading(false);
    }
  };

  const copyToClipboard = async (text: string, successMessage: string) => {
    try {
      await navigator.clipboard.writeText(text);
      setCopySuccess(successMessage);
      setTimeout(() => setCopySuccess(''), 3000);
    } catch (err) {
      console.error('Failed to copy text: ', err);
      setCopySuccess('Failed to copy to clipboard');
      setTimeout(() => setCopySuccess(''), 3000);
    }
  };

  const handleNewArticle = () => {
    setResult(null);
    setError('');
    setCopySuccess('');
    setUrl('');
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-50 to-gray-100 dark:from-gray-900 dark:to-gray-800">
      <div className="container mx-auto px-4 py-16">
        <div className="max-w-3xl mx-auto space-y-8">
          {/* Header */}
          <div className="text-center space-y-4">
            <h1 className="text-4xl font-bold tracking-tight text-gray-900 dark:text-white">
              Article Scraper
            </h1>
            <p className="text-lg text-gray-600 dark:text-gray-300">
              Enter an article URL to extract its content
            </p>
          </div>

          {/* Input Form */}
          {!result && (
            <Card className="bg-white/50 dark:bg-gray-800/50 backdrop-blur-sm">
              <CardContent className="pt-6">
                <form onSubmit={handleScrape} className="space-y-4">
                  <div className="space-y-2">
                    <div className="relative">
                      <input
                        type="url"
                        id="url"
                        name="url"
                        placeholder="https://example.com/article"
                        value={url}
                        onChange={(e) => {
                          const newValue = e.target.value;
                          setUrl(newValue);
                          setError('');
                          setCopySuccess('');
                        }}
                        className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
                        required
                        pattern="https?://.*"
                      />
                    </div>
                  </div>
                  <Button 
                    type="submit" 
                    className="w-full bg-black hover:bg-gray-800 text-white"
                    disabled={loading}
                  >
                    {loading ? 'Scraping...' : 'Scrape Article'}
                  </Button>
                </form>
              </CardContent>
            </Card>
          )}

          {/* Error Message */}
          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {/* Copy Success Message */}
          {copySuccess && (
            <Alert className="bg-green-50 border-green-200 text-green-800 dark:bg-green-900/20 dark:border-green-800 dark:text-green-200">
              <AlertDescription>{copySuccess}</AlertDescription>
            </Alert>
          )}

          {/* Results */}
          {result && (
            <Card className="overflow-hidden">
              <CardHeader className="border-b bg-gray-50/50 dark:bg-gray-800/50">
                <div className="flex justify-between items-start gap-4">
                  <div className="flex-1">
                    <CardTitle className="text-2xl">{result.title}</CardTitle>
                    {result.publishedDate && (
                      <div className="flex items-center gap-2 mt-2">
                        <p className="text-sm text-gray-500 dark:text-gray-400">
                          Published: {new Date(result.publishedDate).toLocaleDateString()}
                        </p>
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => copyToClipboard(new Date(result.publishedDate).toLocaleDateString(), 'Date copied!')}
                          className="text-xs h-6 px-2"
                        >
                          üìÖ Copy Date
                        </Button>
                      </div>
                    )}
                  </div>
                  <div className="flex flex-col sm:flex-row gap-2">
                    <Button
                      onClick={handleNewArticle}
                      className="bg-blue-600 hover:bg-blue-700 text-white text-xs whitespace-nowrap"
                      size="sm"
                    >
                      üîÑ Scrape New Article
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => copyToClipboard(result.title, 'Title copied!')}
                      className="text-xs whitespace-nowrap"
                    >
                      üìã Copy Title
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => copyToClipboard(
                        `Title: ${result.title}\n\nPublished: ${result.publishedDate ? new Date(result.publishedDate).toLocaleDateString() : 'N/A'}\n\nContent:\n${result.content}\n\nSource: ${result.url}`,
                        'All content copied!'
                      )}
                      className="text-xs whitespace-nowrap"
                    >
                      üìÑ Copy All
                    </Button>
                  </div>
                </div>
              </CardHeader>
              <CardContent className="p-6">
                <div className="prose dark:prose-invert max-w-none">
                  <div className="flex justify-between items-center mb-4">
                    <h3 className="text-lg font-semibold m-0">Article Content</h3>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => copyToClipboard(result.content, 'Content copied!')}
                      className="text-xs"
                    >
                      üìù Copy Content
                    </Button>
                  </div>
                  <div className="bg-gray-50 dark:bg-gray-800 rounded-lg p-4 border">
                    <p className="whitespace-pre-line text-sm leading-relaxed m-0">{result.content}</p>
                  </div>
                  
                  {/* Source URL */}
                  <div className="mt-4 pt-4 border-t">
                    <div className="flex justify-between items-center">
                      <p className="text-sm text-gray-600 dark:text-gray-400 m-0">
                        <strong>Source:</strong> {result.url}
                      </p>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => copyToClipboard(result.url, 'URL copied!')}
                        className="text-xs"
                      >
                        üîó Copy URL
                      </Button>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          )}
        </div>
      </div>
    </div>
  );
}
